.homeTransition-appear {
    opacity: 0;
}

.homeTransition-appear.homeTransition-appear-active {
    opacity: 1;
    -webkit-transition: all .5s ease-in-out;
    -o-transition: all .5s ease-in-out;
    transition: all .5s ease-in-out;
}

.home__aside {
    width: 100%;
    text-align: center;
}

.home__main h1 {
    text-align: center;
    margin: 50px 0;
}

.home__main h2 {
    margin-bottom: 0;
    line-height: 1.5;
}

@media only screen and (min-width: 992px) {
    .home {
        display: -ms-flexbox;
        display: flex;
        -ms-flex-pack: space-evenly;
            justify-content: space-evenly;
    }
    .home__main {
        width: 60%;
        max-width: 800px;
    }
    .home__aside {
        width: 30%;
        margin-top: 136px;
        margin-left: 35px;
    }
}


